<template>
  <view class="team-performance-container">
    <u-form :model="form" ref="uForm">
      <u-form-item label="开始日期">
        <u-datetime-picker v-model="form.startDate" mode="date" :max-date="new Date().getTime()"></u-datetime-picker>
      </u-form-item>
      <u-form-item label="结束日期">
        <u-datetime-picker v-model="form.endDate" mode="date" :max-date="new Date().getTime()"></u-datetime-picker>
      </u-form-item>
    </u-form>
    <u-button @click="handleSearch" type="primary">查询</u-button>

    <view class="team-performance-list" v-if="teamPerformance.length > 0">
      <view class="team-performance-item" v-for="(performance, index) in teamPerformance" :key="index">
        <text>员工姓名：{{ performance.name }}</text>
        <text>总业绩：{{ performance.totalPerformance }}</text>
        <text>总分成：{{ performance.totalCommission }}</text>
        <text>总实际：{{ performance.totalActual }}</text>
      </view>
    </view>
    <view v-else>暂无数据</view>

    <u-loadmore :status="loadMoreStatus" @loadmore="loadMore" />
  </view>
</template>

<script>
export default {
  data() {
    return {
      form: {
        startDate: new Date(new Date().setDate(new Date().getDate() - 30)).getTime(), // 默认查询最近30天
        endDate: new Date().getTime()
      },
      teamPerformance: [],
      page: 1,
      pageSize: 20,
      loadMoreStatus: 'loadmore',
      userInfo: null
    }
  },
  onLoad() {
    this.userInfo = uni.getStorageSync('userInfo')
    if (!this.userInfo || !this.userInfo.isAdmin) {
      uni.showToast({
        title: '无权限访问',
        icon: 'none'
      })
      uni.navigateBack()
      return
    }
    this.handleSearch()
  },
  methods: {
    handleSearch() {
      this.page = 1
      this.teamPerformance = []
      this.loadMoreStatus = 'loadmore'
      this.fetchTeamPerformance()
    },
    fetchTeamPerformance() {
      uniCloud.callFunction({
        name: 'queryTeamPerformance',
        data: {
          startDate: this.form.startDate,
          endDate: this.form.endDate,
          page: this.page,
          pageSize: this.pageSize
        }
      }).then(res => {
        if (res.result.code === 0) {
          this.teamPerformance = [...this.teamPerformance, ...res.result.data]
          if (res.result.data.length < this.pageSize) {
            this.loadMoreStatus = 'nomore'
          } else {
            this.loadMoreStatus = 'loadmore'
          }
        } else {
          uni.showToast({
            title: res.result.message,
            icon: 'none'
          })
        }
      }).catch(err => {
        uni.showToast({
          title: '查询失败，请重试',
          icon: 'none'
        })
      })
    },
    loadMore() {
      if (this.loadMoreStatus === 'loadmore') {
        this.page++
        this.fetchTeamPerformance()
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.team-performance-container {
  padding: 20px;
}
.team-performance-list {
  margin-top: 20px;
}
.team-performance-item {
  background-color: #f8f8f8;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
}
</style>